import React from "react"
import { graphql } from "gatsby"
import SiteLayout from "../components/SiteLayout"
import { StyledSection } from "./wp-styled-post-section"

export const postQuery = graphql`

  query($databaseId: Int!) {
		wpgraphql {
			posts(where : {id : $databaseId}){
				edges {
					node {
						id
						databaseId
						title
						content
					}
				}
			}
		}
	}	
`

export default function (data: any) {

	console.log(data.data)

	const { wpgraphql: { posts: { edges } } } = data.data;
	const [{ node: post }] = edges

	return (

		<SiteLayout>
			<StyledSection className="wwf-sg-section">
				<div className="wwf-sg-container container">
					<div className="wwf-sg-row row">
						<div className="wwf-sg-column col">
							<div className="row no-gutters">
								<div className="col-12 px-4 mr-auto entry-content ">
									<div
										style={{ fontSize: 34, fontWeight: 700 }}
										dangerouslySetInnerHTML={{
											__html: post.title
										}}
									/>
									<div
										// className={`px-3 py-2 entry-content `}

										style={{ fontSize: "1.1rem" }}
										dangerouslySetInnerHTML={{
											__html: post.content
										}}
									/>
								</div>
							</div>
						</div>
					</div>
				</div>

				<div className="wwf-sg-section-bg-popover"></div>
			</StyledSection >
		</SiteLayout>
	)
}


